.TeamPage {

    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 10px;

    .home-button-section
    {
        
    }

.team-name-section{

    grid-column: span 3;
    justify-self: start;
    align-self: end;

    .team-name{
        font-size: 4rem;
        color: white;
        margin-bottom: 40px;
    }
}

.win-loss-section{
    
    justify-self: center;
    padding: 30px;
    text-align: center;
}


.match-details-section{

    grid-column: span 4;
}


}
.more-section
{
    align-self: center;
    justify-self: center;
    font-size: 1.5rem;
    color: white;
}
